"mb-2"},[_vm._v(" Cada día se crean nuevas técnicas para exponer o vulnerar sistemas computacionales, y es un gran desafío para la seguridad de la información acompañar esta velocidad, e incluso estar al frente y no actuar de forma reactiva. La implementación de una buena política de IDS es fundamental en una arquitectura de seguridad, ya que este recurso, si se actualiza constantemente, es capaz de mantener la infraestructura distante de ataques oportunistas, ya sea desde una perspectiva de la red, o por la propia exposición de un dispositivo. Para poner en funcionamiento, un sistema de detección de intrusos se debe tener en cuenta que es posible optar por una solución de hardware, de software, o incluso una combinación de estos. La posibilidad de introducir un elemento hardware es debido al alto procesamiento de información en redes con mucho tráfico; a su vez, los registros de firmas y las bases de datos con los posibles ataques necesitan gran cantidad de almacenamiento. En la actualidad, además, el desarrollo de las estrategias de seguridades para los dispositivos y las redes de comunicación ha influido en el surgimiento de un nuevo tipo de defensa a tomar en consideración: los IPS o Sistemas de Prevención de Intrusiones, que en buena medida pueden interpretarse como una evolución de los IDS tradicionales.")]),_c('i',{staticClass:"fas fa-quote-right"})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_6"}},[_c('div',{staticClass:"h2"},[_vm._v("1.6 Vulnerabilidades y amenazas")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5 justify-content-center"},[_c('div',{staticClass:"col-lg-10 mb-5 mb-lg-0"},[_c('div',{staticClass:"bloque-texto-b color-secundario p-0"},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('i',{staticClass:"fas fa-quote-left"}),_c('span',{staticClass:"mb-2"},[_vm._v(" La vulnerabilidad es la debilidad de cualquier tipo que compromete la seguridad del sistema informático. Se define como amenaza el escenario en el que una acción o suceso, ya sea o no deliberado, compromete la seguridad de un elemento del sistema informático. Cuando a un sistema informático se le detecta una vulnerabilidad y existe una amenaza asociada a dicha vulnerabilidad, puede ocurrir que el suceso o evento se produzca y el sistema esté en riesgo. Si el evento se produce y el riesgo que era probable ahora es real, el sistema informático sufrirá daños que habrá que valorar cualitativa y cuantitativamente, y esto se llama 'impacto'. Es decir, un evento producido en el sistema informático que constituye una amenaza asociada a una vulnerabilidad del sistema produce un impacto sobre él. Si se quieren eliminar las vulnerabilidades del sistema informático o queremos disminuir el impacto que puedan producir sobre él, ha de proteger el sistema mediante una serie de medidas que se pueden llamar defensas o salvaguardas."),_c('p',{staticClass:"text-end mb-0"},[_c('b',[_vm._v("- Mifsud, 2012")]),_c('i',{staticClass:"fas fa-quote-right"})])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"371px"},attrs:{"src":require("@/assets/curso/tema1/img15.svg"),"alt":"vulnerabilidades"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"tarjeta-avatar-b mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/cards/img1.svg"),"alt":"AvatarTop"}}),_c('div',{staticClass:"tarjeta tarjeta--azul"},[_c('div',{staticClass:"p-4"},[_c('h3',[_vm._v("Desbordamiento de buffer")]),_c('p',[_vm._v("Ocurre cuando un programa no controla la cantidad de datos que se copian en buffer, si esa cantidad es superior a l